import React, { Component } from "react";
import styled from "styled-components";
import { Link } from "react-scroll";
import "./navbar.css";

class NavBar extends Component {
  render() {
    return (
      <nav>
        <ul class="nav-bar">
          <input type="checkbox" id="check" />
          <span class="menu">
            <li>
              <Link
                activeClass="active"
                spy={true}
                smooth={true}
                duration={500}
                to="home"
              >
                <a href="">Home</a>
              </Link>
            </li>
            <li>
              <Link
                activeClass="active"
                spy={true}
                smooth={true}
                duration={500}
                to="about"
              >
                <a href="">About</a>
              </Link>
            </li>
            <li>
              <Link
                activeClass="active"
                spy={true}
                smooth={true}
                duration={500}
                to="skills"
              >
                <a href="">Skillset</a>
              </Link>
            </li>
            <li>
              <Link
                activeClass="active"
                spy={true}
                smooth={true}
                duration={500}
                to="works"
              >
                <a href="">Work</a>
              </Link>
            </li>{" "}
            <li>
              <Link
                activeClass="active"
                spy={true}
                smooth={true}
                duration={500}
                to="contact"
              >
                <a href="">Contact </a>
              </Link>
            </li>
            <label for="check" class="close-menu">
              <i class="fas fa-times"></i>
            </label>
          </span>
          <label for="check" class="open-menu">
            <i class="fas fa-bars"></i>
          </label>
        </ul>
      </nav>
    );
  }
}

export default NavBar;
